var Input_t = React.createClass({
  getInitialState: function () {
    return {value1:"默认如果对方 值"};
  },
  getValue:function(){return this.state.value1},
  setValue:function(event){ this.setState({value1:event.target.value})},
  render: function () {
    var f=this.state.value1;
    return <div> 
    <input value={this.state.value1} onChange={this.setValue}/>{this.state.value1}
    </div>
  }
});
var NoLink = React.createClass({
  getInitialState: function() {
    return {message: 'Hello!'};
  },
  handleChange: function(event) {
    this.setState({message: event.target.value});
  },
  render: function() {
    var message = this.state.message;{message}
    return <div><input type="text" value={message} onChange={this.handleChange} />{message}</div>;
  }
});
ReactDOM.render(<Greet />, document.getElementById("app"));




